.App {
  text-align: center;
  object-fit: contain;
  min-height: 100vh;
  text-decoration: none;
}

body {

  margin: 0;
  padding: 0;
  font-family: 'Times New Roman', Times, serif;
  font-weight: bolder; 
  text-transform: uppercase;
  text-decoration: none;
  /* Set a default font family */
 
}

.App-logo {
  border-radius:50% ;
  height: 80vmin;
  pointer-events: none;
  object-fit: cover;
  padding: 0%;
  margin: 0%;
  animation: App-logo-spin infinite 200s linear;
  
}

@media (max-width: 768px ,prefers-reduced-motion: no-preference)  {
  .card {
    /* Adjust styles for smaller screens */
    font-size: 14px; /* Example */
    margin: 5px; /* Example */
  }

}

.counter {
  font-size: 34px;
  color: #d7f0f3;
  border: 0.5px rgb(128, 114, 114) solid;
  border-radius: 20px;
  margin-left: 12%;
  margin-right: 12%;
  background-color: #353636;
}

.App-header {
  background: linear-gradient(to bottom, #60436e, #c7a614);
  padding: 20px;
  border-radius: 0px;
  color: #fff;
  text-align: center;
  flex: 1; /* Allow header to grow to fill remaining space */
}
.App-link {
  color: #e8f9fd;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.progress-bar {
  background: grey;
  border-radius: 10px;
  margin: 20px;
  position: relative;
}

.progress {
  background: green;
  border-radius: 10px;
  height: 20px;
}

.progress-text {
  color: white;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

/* Styles for the menu bar */
.menu {
  display: flex;
  justify-content: space-around;
  /* Fix the menu bar at the bottom using position: fixed */
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%; /* Make it span the entire width */
  /* Add some padding for better aesthetics */
  padding: 10px;
  background-color:rgb(209, 26, 66); /* Set the background color */
  border: 1px solid rgb(29, 28, 28);
  border-radius: 20px;
  width:96%;
}

.menu-item {
  background: #333; /* Adjust the background color to match the menu */
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 18px;
}

.buttn {
  border-radius: 50%;
  background-color: transparent;
  box-shadow: none;
  border: 0.5px solid transparent;
  outline: none;
  transition: none;
  -webkit-appearance: none; /* for Safari and Chrome */
  -moz-appearance: none; /* for Firefox */
  appearance: none; /* for modern browsers */
  -webkit-tap-highlight-color: transparent;
}
.buttn:active {
  border-style: outset;
  appearance: none; 
}

.level {
  font-size: 20px;
  color: #dff7fa;
  text-indent: 0.1px;
  border: 0.5px rgb(128, 114, 114) solid;
  border-radius: 20px;
  margin-left: 18%;
  margin-right: 18%;
  background-color: #353636;
}



