.gold-coin-button {
    /* Styles for the gold coin button image */
  }
  
  .coin-number {
    position: absolute;
    color: white;
    font-weight: bold;
    animation: riseUp 0.5s ease-in-out; /* Add the animation here */
    transform: translateY(-${position.y}px); 
}
  
  @keyframes riseUp {
    from {
      transform: translateY(-100%); /* Start from below the button */
    }
    to {
      transform: translateY(0); /* Rise to the top */
    }
  }